*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    display: flex;
    flex-direction: column;
    gap: 16px;

    color: white;
    background: no-repeat center/100% url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    font-family: "Quicksand", "Roboto", Arial, Helvetica, sans-serif;

    padding: 16px;
}

a:link {
    color: inherit;
}

a:hover {
    color: aquamarine;
    text-decoration: none;
}

a:visited {
    color: aqua;
}

#header,
#footer {
    font-size: 24px;
    background-color: rgba(0, 0, 0, 65%);
    padding: 16px;
}

.nav {
    display: flex;
    gap: 16px;
}

.nav-button {
    font-size: inherit;
    font-family: inherit;
    color: inherit;
    background-color: transparent;
    padding: 8px;
    border: none;
    border-bottom: 1px solid transparent;
    cursor: pointer;
    transition: .3s border-bottom ease, .05s transform ease;
    flex: 1;
}

.nav-button.active {
    border-bottom-color: white;
}

.nav-button:hover {
    border-bottom-color: white;
}

.nav-button:active {
    transform: translateY(2px);
}


#content {
    width: 70vw;
    min-height: 70vh;
    background-color: rgba(0, 0, 0, .6);
    margin: 16px;
    padding: 16px;
}

.wrapper {
    display: grid;
    gap: 48px;
}

.desc {
    font-size: 24px;
}

.card-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 16px;
}

.card {
    display: flex;
    gap: 12px;
}

.card-image {
    width: 100px;
    height: auto;
    border-radius: 8px;
}

.card-text {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.card-name {
    display: flex;
    justify-content: space-between;
}


#footer {
    display: flex;
    justify-content: center;
    gap: 8px;
}
